	// import common from "../../common/common.js";
	// 导入自定义组件
	import helloComp from "../../components/helloComp.vue";
	import trailerStars from "../../components/trailerStars.vue"
	export default {
		data() {
			return {
				title: 'Hello',
				carouselList: [],
				hotSuperheroList: [],
				hotTrailerList: [],
				animationData:{},
				guessULikeList: [],
				animationDataArr: [
					{}, {}, {}, {}, {}
				]
				
			}
		},
		
		onUnload() {
// 页面卸载的时候，清除动画数据
			this.animationData = {};
			this.animationDataArr = [
				{}, {}, {}, {}, {}
			];
		},
		onPullDownRefresh() {
			this.refresh();
		},
		onLoad() {
			var me = this;
			

		// 在页面创建的时候，创建一个临时动画对象
		this.animation = uni.createAnimation();

			// 获取common.js 服务器中的url
			// var serverUrl = common.serverUrl;
			// 通过挂载到main.js 中获取服务器的地址，作为全局变量
			var serverUrl = me.serverUrl;
			// 查询热门超英
			uni.request({
				// 请求轮播图数据
				url: serverUrl + '/index/carousel/list', //仅为示例，并非真实接口地址。
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '2573971154'
				},
				success: (res) => {

					console.log("请求轮播图数据");
					// 获取真是数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var carouselList = res.data.data;
						me.carouselList = carouselList;
					}
				}
			});
			uni.request({
				// 请求超英
				url: serverUrl + '/index/movie/hot?type=superhero', //仅为示例，并非真实接口地址。
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '2573971154'
				},
				success: (res) => {

					// 获取真是数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var hotSuperheroList = res.data.data;
						me.hotSuperheroList = hotSuperheroList;
					}
				}
			});
			uni.request({
				// 请求热门
				url: serverUrl + '/index/movie/hot?type=trailer', //仅为示例，并非真实接口地址。
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '2573971154'
				},
				success: (res) => {
				
					console.log("请求热门");
					// 获取真是数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var hotTrailerList = res.data.data;
						me.hotTrailerList = hotTrailerList;
					}
				}
			});uni.request({
				// 请求轮播图数据
				url: serverUrl + '/index/movie/hot?type=trailer', //仅为示例，并非真实接口地址。
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '2573971154'
				},
				success: (res) => {
					
					console.log(res);
					// 获取真是数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var hotTrailerList = res.data.data;
						me.hotTrailerList = hotTrailerList;
					}
				}
			});
		
				this.refresh();
		},
		onHide() {
			if (this.videoContext) {
				this.videoContext.pause();
			}
		},
		methods: {
			// 播放一个视频的时候，需要暂停其他正在播放的视频
			meIsPlaying(){
				var me = this;
				var trailerId = "";
				if(e){
					trailerId = e.currentTarget.dataset.playintindex;
					me.videoContext = uni.createVideoContext(trailerId);
				}
				var hotTrailerList = me.hotTrailerList;
				for(var i = 0;i<hotTrailerList.length;i++){
					var tempId = hotTrailerList[i].id;
					if(tempId!=trailerId){
						uni.createVideoContext(tempId).pause();
					}
				}
			},
			refresh() {
				var me = this;
				
				uni.showLoading({
					mask: true
				});
				uni.showNavigationBarLoading();
				
				
				var serverUrl = me.serverUrl;
				// 查询猜你喜欢数据列表
				uni.request({
					url: serverUrl + '/index/guessULike',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						qq: '2573971154'
					},
					method: "POST",
					success: (res) => {
						// 获取真实数据之前，务必判断状态是否为200
						console.log(res.data)
						if (res.data.status == 200) {
							var guessULikeList = res.data.data;
							me.guessULikeList = guessULikeList;
						}
					},
					complete: () => {
						uni.hideNavigationBarLoading();
						uni.hideLoading();
						uni.stopPullDownRefresh();
					}
				});
			},
			// 实现点赞动画效果
			praiseMe(e) {

				var gIndex = e.currentTarget.dataset.gindex;
				console.log(gIndex);
				// return;
				// 构建动画数据，并且通过step来表示这组动画的完成
				this.animation.translateY(-60).opacity(1).step({
					duration: 400
				});
				
				// 导出动画数据到view组件，实现组件的动画效果
				// this.animationData = this.animation.export();
				this.animationData = this.animation;
				this.animationDataArr[gIndex] = this.animationData.export();
			
				// 还原动画
				setTimeout(function() {
					this.animation.translateY(0).opacity(0).step({
						duration: 0
					});
					this.animationData = this.animation;
					this.animationDataArr[gIndex] = this.animationData.export();
				}.bind(this), 500)

			}
		},
		components:{
			helloComp,
			trailerStars
		}
	}
